$(document).ready(function () {

  $(".modal form").on('submit', function(e){
    e.preventDefault();
    let id = $(this).attr('id');
    $('input[form="'+id+'"]').val('')
    $(".modal").modal('hide');
    $("#successModal").modal('show');

  })


  $(".phone").inputmask("+7 (999) 999-99-99");
  $('#mobilemenu-toogler').on('click', function (e) {
    e.preventDefault();
    showBackFrame();
    showMobileMenu();
  });

  $('.back_frame').on('click', (e) => {
    e.preventDefault();
    hideBackFrame();
    closeMobileMenu();
  });
  $('.footer__menu .menu__title').on('click', function (e) {
    e.preventDefault();
    openFooterMenu($(this));
  });

  $('.left-menu .sub-toogle').on('click', function (e) {
    e.preventDefault();
    openLeftMenuItem($(this));
  });
  $('.left-menu__title').on('click', function (e) {
    $(this).toggleClass('active');
  });

  const swiper = new Swiper('#main-banner', {
    loop: true,

    autoplay: {
      delay: 5000,
    },
    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  });

  const thumbGallery = new Swiper('.thumb-gallery', {
    loop: false,
    direction: 'horizontal',
    spaceBetween: 10,
    slidesPerView: 2,
    freeMode: true,
    watchSlidesProgress: true,
    breakpoints: {
      550: {
        slidesPerView: 3,
      },
      768: {
        slidesPerView: 4,
        direction: 'vertical',
      },
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  });

  const mainGallery = new Swiper('.main-gallery', {
    loop: false,
    spaceBetween: 10,
    slidesPerView: 1,

    thumbs: {
      swiper: thumbGallery,
    },
  });
  function getDirection() {
    var windowWidth = window.innerWidth;
    var direction = window.innerWidth >= 768 ? 'vertical' : 'horizontal';

    return direction;
  }
  $('.product-slider').slick({
    speed: 300,
    slidesToShow: 4,
    slidesToScroll: 4,
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 1,
          infinite: true,
          slidesPerRow: 2,
          rows: 2,
        },
      },
    ],
  });
  $('.col__input').on('click', 'span', function (e) {
    e.preventDefault();
    let input = $(this).parent().find('input');
    let val = Number(input.val());
    if ($(this).hasClass('minus')) {
      val = val - 1;
    } else {
      val = val + 1;
    }
    if (val < 0) {
      val = 0;
    }
    if (val > 9999) {
      val = 9999;
    }
    input.val(val);
  });
  $('.col__input input').on('change input', function (e) {
    if (Number($(this).val()) > 9999) {
      $(this).val(9999);
    }
  });

  // корзина

  $('.cart-item__delete').on('click', function (e) {
    if (confirm('Вы действительно хотите удалить товар из корзины')) {
      $(this).parent().parent().remove();
      showToast('Корзина', 'Товар удален');
    }
  });

  $('.clear-cart').on('click', function (e) {
    e.preventDefault();
    if (confirm('Вы действительно хотите удалить все товары из корзины?')) {
      $('.cart-body').empty();
      showToast('Корзина', 'Корзина очищена');
      setTimeout(() => {
        location.href = '/empty-cart.html';
      }, 3000);
    }
  });
  // Добавление в избраное
  $('.wish').on('click', function (e) {
    e.preventDefault();
    $(this).attr('disabled', true);
    if (!$(this).hasClass('active')){
      $(this).addClass('active');
      setTimeout(() => {
        showToast('Избранноe', 'Товар добавлен в избранное');
        $(this).attr('disabled', false);
      }, 1500);
      return;
    }
    setTimeout(() => {
      showToast('Избранноe', 'Товар удален из избранного');
      $(this).attr('disabled', false);
    }, 1500);
    $(this).removeClass('active');


  });
  //Добавление в корзину
  $('.addCart').on('click', function (e) {
    e.preventDefault();
    $(this).attr('disabled', true);
    
    setTimeout(() => {
      showToast('Корзина', 'Товар добавлен в корзину');
      $(this).attr('disabled', false);

    }, 1500);
  });
});
// -- корзина
const showBackFrame = () => {
  $('.back_frame').addClass('active');
};

const hideBackFrame = () => {
  $('.back_frame').removeClass('active');
};

const showMobileMenu = () => {
  $('#mobile-menu').addClass('active');
};

const closeMobileMenu = () => {
  $('#mobile-menu').removeClass('active');
};

const openFooterMenu = (element) => {
  if (element.hasClass('active')) {
    closeFooterMenu();
    return;
  }
  closeFooterMenu();
  element.addClass('active');
};

const closeFooterMenu = () => {
  $('.footer__menu .menu__title').removeClass('active');
};

const openLeftMenuItem = (element) => {
  if (element.parent().hasClass('active')) {
    closeLeftMenuItem();
    return;
  }
  closeLeftMenuItem();
  element.parent().addClass('active');
};

const closeLeftMenuItem = () => {
  $('.left-menu__item').removeClass('active');
};

const showToast = (head, text) => {
  $.toast({
    text: text, // Text that is to be shown in the toast
    heading: head, // Optional heading to be shown on the toast
    showHideTransition: 'slide', // fade, slide or plain
    allowToastClose: true, // Boolean value true or false
    hideAfter: 3000, // false to make it sticky or number representing the miliseconds as time after which toast needs to be hidden
    stack: 5, // false if there should be only one toast at a time or a number representing the maximum number of toasts to be shown at a time
    position: 'bottom-right', // bottom-left or bottom-right or bottom-center or top-left or top-right or top-center or mid-center or an object representing the left, right, top, bottom values
    bgColor: '#EFEFEF', // Background color of the toast
    textColor: '#595959', // Text color of the toast
    textAlign: 'left', // Text alignment i.e. left, right or center
    loader: true, // Whether to show loader or not. True by default
    loaderBg: '#005BFF', // Background color of the toast loader
    beforeShow: function () {}, // will be triggered before the toast is shown
    afterShown: function () {}, // will be triggered after the toat has been shown
    beforeHide: function () {}, // will be triggered before the toast gets hidden
    afterHidden: function () {}, // will be triggered after the toast has been hidden
  });
};
